.gg-instagram {
    box-sizing: border-box;
    position: relative;
    display: block;
    transform: scale(var(--ggs,1));
    border: 2px solid transparent;
    /* box-shadow: 0 0 0 2px; */
    border-radius: 4px;
    width: 20px;
    height: 20px;
    border-color: #FFB6B9;
   }
   
   .gg-instagram::after,
   .gg-instagram::before {
    content: "";
    display: block;
    box-sizing: border-box;
    position: absolute;
   }
   
   .gg-instagram::after {
    border: 2px solid;
    left: 3px;
    width: 10px;
    height: 10px;
    border-radius: 100%;
    top: 3px;
    border-color: #FFB6B9;
   }
   
   .gg-instagram::before {
    border-radius: 3px;
    width: 2px;
    height: 2px;
    /* background: currentColor; */
    right: 1px;
    top: 1px;
   } 