.box {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate3d(-50%, -50%, 0);
    width: 100%;
    max-width: 95vw;
    padding: 5px;
    border: 2px solid #bae6fd;
  }
  .box:before, .box:after {
    content: "•";
    position: absolute;
    width: 14px;
    height: 14px;
    font-size: 14px;
    color: #bae6fd;
    border: 2px solid #bae6fd;
    line-height: 12px;
    top: 5px;
    text-align: center;
  }
  .box:before {
    left: 5px;
  }
  .box:after {
    right: 5px;
  }
  .box .box-inner {
    position: relative;
    border: 2px solid #bae6fd;
    padding: 40px;
  }
  .box .box-inner:before, .box .box-inner:after {
    content: "•";
    position: absolute;
    width: 14px;
    height: 14px;
    font-size: 14px;
    color: #bae6fd;
    border: 2px solid #bae6fd;
    line-height: 12px;
    bottom: -2px;
    text-align: center;
  }
  .box .box-inner:before {
    left: -2px;
  }
  .box .box-inner:after {
    right: -2px;
  }

